import request from '@/utils/request';

export function listBrand(query) {
  return request({
    url: '/brand/list',
    method: 'get',
    params: query
  });
}

export function createBrand(data) {
  return request({
    url: '/brand/create',
    method: 'post',
    data: data
  });
}

export function readBrand(data) {
  return request({
    url: '/brand/read',
    method: 'get',
    data: data
  });
}

export function updateBrand(data) {
  return request({
    url: '/brand/update',
    method: 'post',
    data: data
  });
}

export function deleteBrand(data) {
  return request({
    url: '/brand/delete',
    method: 'post',
    data: data
  });
}

export function listCatAndAdmin() {
  return request({
    url: '/brand/catAndAdmin',
    method: 'get'
  });
}